const Map = () => {
    const [hoveredRegion, setHoveredRegion] = useState(null);
    const svgRef = useRef(null);

    const handleMouseEnter = (regionId) => {
        setHoveredRegion(regionId);
    };

    const handleMouseLeave = () => {
        setHoveredRegion(null);
    };

    const mapSvg = (
        <div
            ref={svgRef}
            dangerouslySetInnerHTML={{ __html: svgData }}
            onMouseEnter={() => handleMouseEnter('map')}
            onMouseLeave={() => handleMouseLeave()}
        />
    );

    return (
        <div>
            {mapSvg}
            <p>Hovered Region: {hoveredRegion ? hoveredRegion : 'None'}</p>
            <style jsx>
                {`
          path:hover {
            fill: #f00; /* Change the fill color on hover as an example */
            cursor: pointer;
          }
        `}
            </style>
            <script>{`console.log('This is where your interactive SVG map script could go.');`}</script>
        </div>
    );
};

export default Map;
